
import styles from '../Modal.module.scss';
import  {AiFillCloseCircle} from 'react-icons/ai'
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { useState, useEffect } from 'react';
import http from 'src/http';
import { Title } from 'src/components/Title/Title';
import { useContext } from 'react';
import {LoadingButton} from '@mui/lab';
import {TextField} from '@mui/material'
import { UserContext } from 'src/context/UserContext';
import { ICategory } from 'src/models/product';


export const CategoryModal = ({openModal, setModal, data, edit}: ModalPropsTestEdit) => {

  const [editedCategory, setEditedCategory] = useState<string>('');
  const [newCategory, setNewCategory] = useState<string>("");
  const [idCategory, setIdCategory] = useState<string>('');
  const [loading, setLoading] = useState<Boolean>(false);
  const [message, setMessage] = useState<string>('');
  const [error, setError] = useState<string>('');


  const {
    usuario
  } = useContext(UserContext);



  async function editCategoryById(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.put<ICategory>(`/categorias/${idCategory}`, {
      descricao: editedCategory 
    })
      .then(response=>{
          if(response.status == 202){
            setMessage("Categoria editada com sucesso")
          }
      }).catch(error=>
        setError("Erro no servidor, por favor, tente novamente.")
      )
    setLoading(false);
    window.location.reload();
  }




  async function createCategory(event: React.FormEvent<HTMLFormElement>){
    event.preventDefault();
    setLoading(true);
    http.post<ICategory>('/categorias', {
      descricao: newCategory 
    })
      .then(response=>{
          if(response.status == 202){
            setMessage("Categoria criada com sucesso")
          }
      }).catch(error=>
        setError("Erro no servidor, por favor, tente novamente.")
      )
    setLoading(false);
    window.location.reload();
  }

  useEffect(()=>{
    if(data){
      setEditedCategory(data?.descricao);
      setIdCategory(data?.id);
    }
  }, [data]);



  return (
    <>
      {openModal && (
        <div className={styles.modalCategory} style={{backgroundColor: 'rgb(255, 255, 255)'}}>
          <div className={styles.modal__divCloseButton}>
            <button 
                className={styles.modal__divCloseButton__closeButton}
                onClick={(e)=>setModal(!openModal)}
              >
              <AiFillCloseCircle/>
            </button>
          </div>
          
          <Title>Nova categoria</Title>
          <form onSubmit={edit ? editCategoryById: createCategory}>
            <div style={{marginTop: '1rem'}}>
              <TextField 
                  id="category_name"
                  type="text"
                  label="Nome da categoria"
                  fullWidth={true}
                  required={true}
                  color="primary"
                  placeholder="Digite o nome da categoria"
                  onChange={ edit? (e)=>setEditedCategory(e.target.value): (e)=>setNewCategory(e.target.value)}
                  value={edit? editedCategory: newCategory}
                />
            </div>
            {usuario?.id_perfil === 3? '':
              <div style={{ display: 'flex', gridGap: '1rem', alignItems: 'center', marginTop: '1rem'}}>
                {edit ? 
                  <LoadingButton 
                    color='primary' 
                    type='submit' 
                    variant='contained' 
                    size='medium'
                    loading={loading?true: false}
                  >
                  Editar
                </LoadingButton> 
                  :
                <LoadingButton 
                    color='primary' 
                    type='submit' 
                    variant='contained' 
                    size='medium'
                    loading={loading?true: false}
                  >
                  Cadastrar
                </LoadingButton>
                }
                <LoadingButton 
                  color='warning' 
                  size='medium'
                  variant='contained' 
                  onClick={(e)=>setModal(!openModal)}
                  >
                  Cancelar
                </LoadingButton>
              </div>
            }

            <div>
              {!loading && message && (
                <p style={{color: 'green'}}>{message}</p>
              )}

              {!loading && error && (
                <p style={{color: 'red'}}>{error}</p>
              )}    
            </div>

          </form>
            
        </div>
      )}
    </>
  )
}
