import { FC, ChangeEvent, useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import { api } from 'src/api/api';
import {
  Tooltip,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';

import { read, utils, writeFileXLSX } from 'xlsx';
import { ICategory } from 'src/models/product';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import BulkActions from '../Category/BulkActions';
import {FaCloudDownloadAlt} from 'react-icons/fa'
import { CategoryModal } from 'src/components/Modals/CategoryModal/CategoryModel';

interface CategoryTableProps {
  className: string;
  categories: ICategory[];
}



const applyFilters = (
  categories: ICategory[],
): ICategory[] => {
  return categories.filter((category) => {
    let matches = true;

    /*if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  categories: ICategory[],
  page: number,
  limit: number
): ICategory[] => {
  return categories.slice(page * limit, page * limit + limit);
};

const CategoryTables: FC<CategoryTableProps> = ({ categories }) => {


  const [selectedCategories, setSelectedCategories] = useState<string[]>([]);
  const selectedBulkActions = selectedCategories.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);

  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(categories);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Categorias.xlsx");
  }, [categories]);

  const handleSelectAllCategories = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedCategories(
      event.target.checked
        ? categories.map((category) => category.id)
        : []
    );
  };

  const handleSelectOneCategory = (
    event: ChangeEvent<HTMLInputElement>,
    category: string
  ): void => {
    if (!selectedCategories.includes(category)) {
      setSelectedCategories((prevSelected) => [
        ...prevSelected,
        category
      ]);
    } else {
      setSelectedCategories((prevSelected) =>
        prevSelected.filter((id) => id !== category)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredCategories = applyFilters(categories);
  const paginatedCategories = applyPagination(
    filteredCategories,
    page,
    limit
  );
  const selectedSomeCategories =
  selectedCategories.length > 0 &&
  selectedCategories.length < categories.length;
  const selectedAllCategories =
    selectedCategories.length === categories.length;
  const theme = useTheme();


  const [data, setData] = useState<ICategory[]>([]);
  const [idCategory, setIdCategory] = useState<ICategory[]>([]);
  const [modal, setModal] = useState<Boolean>(false)

  async function queryCategoryId(idCategory: any){
    setModal(!modal)
    setData(null)
    const {data} = await api.get(`/categorias/${idCategory}`);
    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }


  return (
    <Card>
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions id_category={selectedCategories}/>
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
              <FaCloudDownloadAlt onClick={exportFile} style={{
                textAlign: 'right',
                position: 'relative',
                left: '7rem',
                height: '25px',
                width: '25px',
                cursor: 'pointer'
              }}/>
            </Box>
          }
          title="Categorias cadastradas"
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                
              </TableCell>
              <TableCell>Detalhes categoria</TableCell>
              <TableCell>ID categoria</TableCell>
              <TableCell align='right'>Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedCategories.map((category) => {
              const isCategorySelected = selectedCategories.includes(
                category.id
              );
              return (
                <TableRow
                  hover
                  key={category.id}
                  selected={isCategorySelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isCategorySelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneCategory(event, category.id)
                      }
                      value={isCategorySelected}
                    />
                  </TableCell>

                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {category.descricao}
                    </Typography>
                  </TableCell>

                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {category.id}
                    </Typography>
                  </TableCell>

                  <TableCell align="right">
                    <Tooltip title="Editar categoria" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryCategoryId(category.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                  </TableCell>

                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredCategories.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      {data  && modal && (
        <CategoryModal 
        openModal={modal}
        setModal={setModal}
        data={data}
        edit={true}
      />
      )}
    </Card>
  );
};

CategoryTables.propTypes = {
  categories: PropTypes.array.isRequired
};

CategoryTables.defaultProps = {
  categories: []
};

export default CategoryTables;
